//import App from "../App";
import { Container, Navbar } from 'react-bootstrap';
const Menu = () => {
    return (
        <Navbar bg="dark">
            <Container>
                <Navbar.Brand href="/">Home</Navbar.Brand>
                <Navbar.Toggle />
                <Navbar.Collapse className="justify-content-end">
                    <Navbar.Text>
                        <a href="#login">Login</a>
                    </Navbar.Text>
                    <Navbar.Text>
                        <a href="#sign">Signin</a>
                    </Navbar.Text>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}
export default Menu;